import { createSlice } from '@reduxjs/toolkit';
import { IGraphic } from '../../../pages/MainPage/slices/GraphicSlice';
interface IPreset{
    id: number,
    name: string,
    settings: IGraphic[],
    mode: string
}
const initialState: IPreset[]=[]
const PresetSlice = createSlice({
    name: 'preset',
    initialState,
    reducers: {
        addPreset(state, action){
            if(state.length===0){
                state.push({id: 1, name: action.payload.name, settings: action.payload.settings, mode: action.payload.mode})
            }else{
                console.log(state[state.length-1])
                state.push({id: state[state.length-1].id+1, name: action.payload.name,settings: action.payload.settings, mode: action.payload.mode})
            }
        }
    },
});

export const {addPreset} = PresetSlice.actions;

export default PresetSlice.reducer;